import { graphql } from "gatsby";
import * as React from "react";

import Layout from "../layout/layout";

import "swiper/swiper.scss";
import Swiper from "react-id-swiper";
import styled from "styled-components";

const HeadingStyle = styled.section`
    width: 1200px;
    margin: 0 auto;
    position: relative;
    margin-top: 150px;
    height: 400px;
    .tit {
        font-size: 54px;
        color: #444;
        letter-spacing: -7px;
        font-weight: 300;
        margin-left: 100px;
        span {
            display: block;
            margin-top: -15px;
            &.lined:after {
                content: "";
                position: absolute;
                top: 194px;
                left: 235px;
                width: 250px;
                height: 3px;
                background: #444;
            }
        }
    }
    .desc {
        position: absolute;
        left: 296px;
        top: 205px;
        text-align: right;
        color: #a7a7a7;
        font-size: 12px;
        font-weight: 100;
        span {
            margin-top: -5px;
        }
    }
    .desc_more {
        position: absolute;
        width: 200px;
        top: 100px;
        right: 40px;
        font-weight: 100;
        font-size: 11px;
        color: #989191;
        span {
            margin-top: 0;
        }
        .desc_more {
            right: 220px;
            top: 136px;
        }
    }
`;
const SliderStyle = styled.section`
    width: 1200px;
    margin: 0 auto;
    position: relative;
    margin-top: 150px;
    .desc_wrap {
        position: relative;
        width: 400px;
        overflow: hidden;
        height: 600px;
        ul {
            position: absolute;
            top: 0;
            left: 0;
            width: 1600px;
            overflow: hidden;
            li {
                width: 400px;
                height: 600px;
                .desc {
                    padding-top: 170px;
                    font-weight: 100;
                    font-size: 11px;
                    color: #989191;
                    span {
                        display: block;
                    }
                }
            }
        }
    }
    .slider {
        position: absolute;
        top: 0;
        right: 40px;
        width: 762px;
        overflow: hidden;
    }
`;
const ImgSectionStyle = styled.section`
    width: 1200px;
    margin: 0 auto;
    position: relative;
    margin-top: 500px;
    &:before {
        content: "";
        position: absolute;
        top: -230px;
        left: 50%;
        margin-left: -50px;
        background: #444;
        height: 3px;
        width: 100px;
    }
    img {
        margin-left: 276px;
    }

    &:after {
        content: "";
        position: absolute;
        bottom: -130px;
        left: 50%;
        margin-left: -50px;
        background: #444;
        height: 3px;
        width: 100px;
    }
`;
const TextStyle = styled.section`
    width: 100%;
    margin: 0 auto;
    position: relative;
    margin-top: 300px;
    margin-bottom: 300px;
    text-align: center;
    overflow: hidden;

    &:before {
        content: "";
        position: absolute;
        top: 31%;
        width: 800px;
        height: 1px;
        background: #eee;
        left: -100px;
    }

    span {
        display: block;
        text-align: center;
        font-size: 13px;
        color: #7b7a7a;
    }

    &:after {
        content: "";
        position: absolute;
        top: 31%;
        width: 800px;
        height: 1px;
        background: #eee;
        right: -100px;
    }
`;
const DesktopStyle = styled.section`
    position: relative;
    height: 879px;
    overflow: hidden;
    .list {
        position: absolute;
        height: 3516px;
        top: 0;
        left: 50%;
        margin-left: -960px;
    }
`;

